
import axios from "axios"

export const loginService= async (userName,password,roleType)=>{
       
     const response = await axios.post(`http://localhost:8080/insuranceapp/login`,{
        
            userName,
            password,
            roleType
        
     })

     return response;
      
}

export const validateUser= async (authToken)=>{
     try{
   
     console.log("inside validator >>>>>>>>>>>>>>>>>>>>>>>>>>>>>",authToken)
   
   let response = await axios.get(`http://localhost:8084/bankapp/validator`,{
   
     headers:
     {
       Authorization: authToken
     }
   
   
   })
   
   console.log("response value is ----------------",response)
   return response;
   } catch (error) {
     throw error
   }
   
   }
   
   