import React, { useEffect, useState } from 'react'
import { getAccounts } from '../../services/customer/CustomerService';
import PaginationApp from '../shared/page/PaginationApp';
import PageSizeSetter from '../shared/page/PageSizeSetter';
import Table from '../shared/table/Table'
import Payments from '../customer/Payments'
import Navbar from '../shared/navbar/Navbar'
import Footer from '../shared/footer/Footer'
import { addPayment, getClaim, paylist } from '../../services/policy/Policy';
import AddPayment from './AddPayment';
import { successAlet, warningAlert } from '../alerts/Alert';
import Claim from './Claim';
  


function ShowAccounts() {

const [account,setAccount] =useState([])
const [pageNumber, setPageNumber] = useState(0);
const [pageSize, setPageSize] = useState(2);
const [totalPages, setTotalPages] = useState();
const [totalElements, setTotalElements] = useState('');
const [data, setData] = useState([])
const [action,setAction]=useState([]);
const [show,setShow]=useState(false);
const [payment,setpayment]=useState([]);
const [policyId,setPolicyId]=useState(0);
const [paymentId,setPaymentId]=useState('')
const [paymentType,setPaymentType]=useState('')
const [accountNo,setAccountNo]=useState('')
const [cardNumber,setCardNumber]=useState("");
const [cvv,setCvv]=useState("");
const [expiry,setExpiry]=useState("");
const [amount,setAmount]=useState(0);
const [show1,setshow1]=useState(false);
const [claim,setClaim]=useState(false)
const [claimAmount,setClaimAmount]=useState();
const [bankName,setBankName]=useState();
const [branchName,setBranchName]=useState();
const [ifscCode,setIfscCode]=useState();
const [show2,setShow2]=useState();


const allAccounts=async()=>
{
    let val =[];
    let response=await getAccounts(pageNumber,pageSize,localStorage.getItem('username'));
    setData(response.data.content);
    console.log("resp000 ",response.data.content,"oooooo",response.headers['customer-account'])
    setTotalPages(Math.ceil(parseInt(response.headers['customer-account']) / pageSize));
    setTotalElements(Math.ceil(parseInt(response.headers['customer-account']) / pageSize));
    console.log("status value is ss",response.data.content.status)
    response.data.content.forEach((element) => {
    let v={
        policyNo:element.policyNo,
        SchemeName:element.insuranceScheme,
        Status:element.status
    }
      val.push(v)
  
    });
    setAccount(val);

}

const getPayments=async()=>{
    if(policyId!=0)
 {
  let payments = await paylist(policyId);
  console.log("payments =========",payments);
  let flag= true;
  payments.data.forEach((p)=>
 {
  p.paymentDate=p.paymentDate.substring(0,10);
  if(p.paymentStatus=="UNPAID")
   flag=false;
 }

 )
  setClaim(flag);
  setpayment(payments.data)
  setShow(true)
}
}


const paymentHandler=(detail)=>
{
    console.log("detail is =90990000000000",detail.policyNo)
    setPolicyId(detail.policyNo)
    
}

let param=
{
  username:localStorage.getItem('username'),
	policyId,
	paymentId,
	paymentType,
	amount,
	cardNumber,
	cvv,
  expiry,
}


const payInHandler=async()=>
{
    try {
    console.log("param value is1111111111111111111111111111111111111111111111",param)    
    let pay = await addPayment(param);
    console.log("pay 9909777777777777",pay);
    setAction(pay)
    if(pay)
    successAlet("Payment Done!")
        
    } catch (error) {
        warningAlert(error.response.data.message)
    }
}
  let param1={
    policyId,
    claimAmount,
    bankName,
    branchName,
    bankAccountNumber:accountNo,
    ifscCode

  }
  const applyclaim = async()=>
  {
    let response = await getClaim(param1);
    console.log("claim responce is param1-%%%%%%%%%%%%%%%%%%%%%%--",response);
    
  }




let d1 = {
  show: true, // Always set to true to show AddPayment component
  setShow: setshow1,
  paymentType,
  amount,
  paymentId,
  cardNumber,
  cvv,
  expiry,

  setPaymentType,
  setPaymentId,
  setCardNumber,
  setCvv,
  setExpiry,
  payInHandler
};

let d2=
{
  show: true, 
  setShow: setShow2,
  claimAmount,
  bankName,
  branchName,
  bankAccountNumber:accountNo,
  ifscCode,
  
  setBankName,
  setBranchName,
  setIfscCode,
  setAccountNo,
  setClaimAmount,
  setAccountNo,
  applyclaim,
}

console.log("7777777777777777777777777777777777",policyId);


const claimHandler=()=>
{
   setShow2(true);
   
   data.forEach((a)=>
   {
    if(a.policyNo==policyId)
    setClaimAmount(a.sumAssured);
   })

   
   

}
   
   



const payHandler=(pdata)=>
{  
   setAmount(pdata.amount)
   setPaymentId(pdata.paymentId)
   setshow1(true)
}

useEffect(
()=>{
allAccounts()
},[pageNumber,totalElements,pageSize])


useEffect(
    ()=>{
    getPayments()
    },[action,policyId])
    


return (
 <>
 <Navbar></Navbar>

  <div className='conatiner'>

   {show2 &&<Claim data ={d2}></Claim>}
   {show1 &&<AddPayment data ={d1}></AddPayment>}

      <div className='row my-5'>
          <div className='offset-2 col-2'>
          <PaginationApp
              totalpage={totalPages}
              setpage={setPageNumber}
              pageNumber={pageNumber}
            ></PaginationApp>
          </div>

          <div className='col-3 offset-1'>

              <input className='rounded-pill px-3 text-primary fw-bold'
                  placeholder='search here'
              ></input>

          </div>
          <div className='col-2'>
          <PageSizeSetter
              setPageSize={setPageSize}
              setTotalpage={setTotalPages}
              totalrecord={totalElements}
              pageSize={pageSize}
              setPageNumber={setPageNumber}
            ></PageSizeSetter>
          </div>
      </div>

      <div className='row'>
          <div className='col-8 offset-2 table-responsive mb-5'>
            <div className='h1 text-bg-dark text-center my-5'>Customer Account</div>
              <Table                
                  data={account}
                  isPayment={true}
                  paymentFun={paymentHandler}     
                   ></Table>
            </div>
          </div>
    </div>
    {claim &&
    <div className='offset-2 col-1 py-2 btn btn-large btn-primary fw-bold'
    onClick={()=>
    
      claimHandler()
    }
    >CLAIM</div>
    }
    {show!=0? <div>
    <div className='row'>
    <div className='h1 offset-2 col-8 text-bg-dark text-center my-5'>Payments</div>
    <div className='offset-2 col-8 '>
    <Table data={payment}
    isPay={true}
    payFun={payHandler}
    ></Table>
    </div>
    </div>
    </div>:null}

   
    
  {
      account==0?
      <div className='text-center fw-bold text-danger fs-1'> No Account Found </div>:null
  }

   <Footer></Footer> 
</>
  )
}

export default ShowAccounts