import React, { useEffect, useState } from 'react'
import plan from '../../images/plan.jpg'
import Navbar from '../shared/navbar/Navbar'
import Footer from '../shared/footer/Footer'
import { useNavigate } from 'react-router-dom'

const Admin = () => {

    const [valid, setValid] = useState(false);
    const navigate=new useNavigate();

    const validateUser = () => {
        if (localStorage.getItem('auth') == null || localStorage.getItem('Role') == null || localStorage.getItem('Role') != 'ROLE_ADMIN') {
            alert("You are not logged in ")
            navigate('/login');
        }
        setValid(true);
    }

    useEffect(
        ()=>{
            validateUser();
        }
        ,[]
    )

    const employeeHandler=()=>{
        navigate('/all_employee')
    }


    return (
        
        <div>
            <Navbar></Navbar>
            <div className='bg-warning text-center display-3 py-3 text-dark fw-bold'>Admin Dashboard</div>
            <div className='container'>
                
                <div className='row my-5'>
                    <div className='col-4'>
                        <div class="card d-flex bg1">
                            <div class="card-body d-flex align-items-center">
                                <div className='d-block '>
                                    <div className='text-center p-5 fw-bold fs-1'>Employees</div>
                                    <button className='btn btn-lg btn-outline-success'
                                     
                                     onClick={employeeHandler}

                                    >View More</button>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className='col-4'>
                        <div class="card d-flex bg1">
                            <div class="card-body d-flex align-items-center">
                                <div className='d-block '>
                                    <div className='text fw-bold p-5 fs-1'>Plans</div>
                                    <button className='btn btn-lg btn-outline-success'
                                    onClick={
                                        ()=>{
                                            navigate('/all_Plan')
                                        }
                                    }
                                    >View More</button>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className='col-4'>
                        <div class="card d-flex bg1">
                            <div class="card-body d-flex align-items-center">
                                <div className='d-block  ms-3'>
                                    <div className='text fw-bold p-5 fs-1 '>Schemes</div>
                                    <button className='btn btn-lg btn-outline-success'
                                    onClick={()=>navigate('/schemes')}
                                    >View More</button>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <div className='row my-5 justify-content-center'>
                    <div className='col-4'>
                        <div class="card d-flex bg1">
                            <div class="card-body d-flex align-items-center">
                                <div className='d-block '>
                                    <div className='text fw-bold p-5 fs-1'>Agents</div>
                                    <button className='btn btn-lg btn-outline-success'
                                    onClick={
                                        ()=>{
                                            navigate('/add_agent')
                                        }
                                    }
                                    >View More</button>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className='col-4'>
                        <div class="card d-flex bg1">
                            <div class="card-body d-flex align-items-center">
                                <div className='d-block '>
                                    <div className='text fw-bold p-5 fs-1'>Claims</div>
                                    <button className='btn btn-lg btn-outline-success'
                                    onClick={
                                        ()=>{
                                            navigate('/admin/claim_home')
                                            }
                                        }>View More</button>
                                </div>

                            </div>
                        </div>
                    </div>
                    <div className='col-4'>
                        <div class="card d-flex bg1">
                            <div class="card-body d-flex align-items-center">
                                <div className='d-block '>
                                    <div className='text fw-bold p-5 fs-1'>Accounts</div>
                                    <button className='btn btn-lg btn-outline-success'
                                    onClick={
                                        ()=>{
                                            navigate('/get_all_policy')
                                            }
                                        }>View More</button>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <Footer></Footer>

        </div>
    )
}

export default Admin