import React, { useEffect, useState } from 'react'
import { deleteEmployee, getAllEmployees, saveEmployee } from '../../services/admin/AdminServices';
import Table from '../shared/table/Table'
import Navbar from '../shared/navbar/Navbar';
import Footer from '../shared/navbar/Navbar';
import PaginationApp from '../shared/page/PaginationApp'
import PageSizeSetter from '../shared/page/PageSizeSetter'
import AddEmployee from './AddEmployee';
import { EditEmployeeService }from '../../services/employee/Employee';
import EditEmployee from './EditEmployee';


const AllEmployee = () => {

    const [pageNumber, setPageNumber] = useState(0);
    const [pageSize, setPageSize] = useState(2);
    const [totalPages, setTotalPages] = useState();
    const [totalElements, setTotalElements] = useState();

    const [employeeData, setEmployeeData] = useState([])
    
    const [id,setId]=useState()
    const [firstName,setFirstName]=useState("");
    const [lastName,setLastName]=useState("");
    const [mobileNumber,setMobileNumber]=useState("")
    const [salary,setSalary]=useState("");
    const [username,setUsername]=useState("");
    const [password,setPassword]=useState("");
    const [email,setEmail]=useState("");
    const [dateOfBirth,setDateOfBirth]=useState("");
    const [houseNo,setHouseNo]=useState("");
    const [apartment,setApartment]=useState("");
    const [city,setCity]=useState("");
    const [state,setState]=useState("");
    const [pincode,setPincode]=useState("");
    const [show ,setShow]=useState(false);
    const [actionData,setActionData]=useState("");
    const [add ,setAdd]=useState(false);


    let data={}

    const addEmployeeHandler=async()=>{
        
        
        let response=await saveEmployee(data);
        console.log(response);
        //alert("employee added successfully!")

    }


    const addEmployeeData={
        firstName,
        setFirstName,
        lastName,
        setLastName,
        mobileNumber,
        setMobileNumber,
        salary,
        setSalary,
        username,
        setUsername,
        password,
        setPassword,
        email,
        setEmail,
        dateOfBirth,
        setDateOfBirth,
        houseNo,setHouseNo,
        apartment,
        setApartment,
        city,
        setCity,
        state,
        setState,
        pincode,setPincode,
        show:add,setShow:setAdd,
        addEmployeeHandler
    }

     data={
        firstName,
        lastName,
        mobileNumber,
        salary,
        username,
        password,
        email,dateOfBirth,
        houseNo,
        apartment,
        city,
        state,pincode
    }

    
    const getEmployeesData = async () => {

        let response = await getAllEmployees(pageNumber, pageSize)
        console.log("data value is------- ",response);
        setEmployeeData(response.data.content);
        setTotalPages(Math.ceil(parseInt(response.headers['employee-count']) / pageSize));
        setTotalElements(Math.ceil(parseInt(response.headers['employee-count']) / pageSize));

        console.log(response);


    }

    const handleDelete=async(employee)=>{

       let response= await deleteEmployee(employee.employeeId)

       setActionData(response);
    }

    const updateEmployee = (employee) => {
        setFirstName(employee.firstName);
        setLastName(employee.lastName);
        setEmail(employee.email);
        setMobileNumber(employee.mobileNumber);
        setId(employee.employeeId);
        setDateOfBirth(employee.dateOfBirth)
        setShow(true);
    
        console.log(
          "values are&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&",
          employee.firstName,
          employee.lastName,
          employee.email,
          employee.mobileNumber,
          employee.employeeId
        );

      };

    const EditData = async () => {

        try{
          
        let response = await EditEmployeeService(
            id,    
            firstName,
            lastName,
            mobileNumber,
            email,
            dateOfBirth
              
        );
        alert("Employee updated successfully!");
        setActionData(response);
    
      } catch (error) {
        alert(error.response)
      }
      };

    useEffect(
        () => {
            getEmployeesData();
        }
        , [pageNumber, pageSize, totalPages, totalElements,actionData])

    return (
        <>
        <Navbar></Navbar>
        {show && (<EditEmployee
          firstName={firstName}
          lastName={lastName}
          mobile={mobileNumber}
          email={email}
          dob={dateOfBirth}
          show={show}
          setFirstName={setFirstName}
          setLastName={setLastName}
          setMobile={setMobileNumber}
          setEmail={setEmail}
          setShow={setShow}
          setDateOfBirth={setDateOfBirth}
          editData={EditData}
        ></EditEmployee>
        )}
        
        {add &&
        (
        <AddEmployee data={addEmployeeData}></AddEmployee>
        )}
         
        
        <div className='background2 text-center display-3 py-3 text-white fw-bold'>All Employees</div>
            <div className='container'>
            <div className='row my-5'>
            <div className="col-4 offset-1">
            <PaginationApp
              totalpage={totalPages}
              setpage={setPageNumber}
              pageNumber={pageNumber}
            ></PaginationApp>
          </div>

        <div className='col-4'>

                <input className='rounded-pill px-3 text-primary fw-bold'
                 placeholder='search here'
                 ></input>

        </div>
        <div className='col-2'>
            <PageSizeSetter
              setPageSize={setPageSize}
              setTotalpage={setTotalPages}
              totalrecord={totalElements}
              pageSize={pageSize}
              setPageNumber={setPageNumber}
            ></PageSizeSetter>
        </div>
        </div>
        <div className='row'>

             <div className='col-12'>

             <button className='btn btn-lg btn-primary fw-bold m-2'
            onClick={()=>setAdd(true)
             }
             >
             Add A New Employee
            </button>

            </div>

            <div className='col-12'>

                            <Table 
                            
                            data={employeeData}
                            isDeleteButton={true}
                            isUpdateButton={true}
                            deleteFun={handleDelete}
                            UpdateFun={updateEmployee}
                            
                            ></Table>

              
                        </div>
                    </div>
                </div>
            
            
        </>
    )
}

export default AllEmployee